'use strict';

function getAlignItems() {
	if (document.documentElement.clientWidth > 991) {
		var alignItems = document.getElementsByClassName('height-align');
		var maxItemlHeight = 0;

		for (var i = 0; i < alignItems.length; i++) {
			if (maxItemlHeight < alignItems[i].clientHeight) {
				maxItemlHeight = alignItems[i].clientHeight;
			}
		}

		maxItemlHeight += 'px';

		for (var _i = 0; _i < alignItems.length; _i++) {
			alignItems[_i].style.height = maxItemlHeight;
		}
	}
}

getAlignItems();